.App{
    display: flex;
    box-sizing: border-box;
    margin: 5px;
    margin-right: 10px;
    border: 2px solid red;
    width: fit-content;
    max-height: 414px;
    max-width: 657px;
    border-radius: 10px;
    background-image: url(https://www.fondoin.gob.ve/wp-content/uploads/2022/08/PORTADA.png);
    background-repeat: no-repeat;
    background-size: cover;
}
h1, h2{
    color: white;
    text-shadow: 2px 2px 2px black;
}
@media only screen and (max-width: 640px) {
    .App{
        flex-direction: column;
        max-height: fit-content;
        width: calc(100% - 10px);
        align-content: center;
    }
}
